import React, { useEffect } from "react";
import "./Letter.scss";

function Letter() {
  useEffect(() => {
    const script = document.createElement("script");

    script.innerHTML = `// set up text to print, each item in array is new line
    var aText = new Array(
    "Cục Bông của a ơi 🤗", 
    "Rứa là lại tới sinh nhựt của em rùi nè, năm ni a có làm màu hơn mọi khi hehehe 🎈🎉✨",
    "Hơi buồn tí năm ni sinh nhật a lại chưa thể về với em được 😭. Buồn ghê vậy lun ớ",
    "nhưng em yêu cũng đừng lo hí, anh sẽ về với em sớm thui mà hihi. Năm ni sinh nhật",
    "em sẽ không phải tiếp sếp đồ chi mệt mỏi nữa nè. Xung quanh em bây giờ đã có gia đình",
    "và bạn bè, chắc chắn sẽ hạnh phúc hơn nhìu phải không em 🤗",
    "Thời gian vừa qua chắc chắn đã có nhiều khó khăn và biến động, nhưng anh hiểu rằng",
    "tất cả mọi thứ sẽ sớm ổn thui nà em. Chỉ cần giữ vững hi vọng, niềm tin, em yêu của anh",
    "chắc chắn sẽ làm được những điều mình mong muốn thui nà. Bên cạnh em luôn luôn có anh,",
    "có gia đình và bạn bè luôn giúp đỡ, em hãy lun nhớ điều đó nhé. Hãy quên hết mọi",
    "nỗi buồn và tận hưởng sinh nhật 26 thật hạnh phúc em nha 🥰",
    "Chúc em tuổi mới thật nhiều niềm vui, luôn luôn tươi trẻ, xinh đẹp, hồn nhiên",
    "và tràn đầy sức sống như vậy nhé!!",
    "Everything will be all right! My Girl 💖💖💖!",
    "Anh của cục bông 💖💖💖"
    );
    var iSpeed = 70; // time delay of print out
    var iIndex = 0; // start printing array at this posision
    var iArrLength = aText[0].length; // the length of the text array
    var iScrollAt = 20; // start scrolling up at this many lines
     
    var iTextPos = 0; // initialise text position
    var sContents = ''; // initialise contents variable
    var iRow; // initialise current row
     
    function typewriter()
    {
     sContents =  ' ';
     iRow = Math.max(0, iIndex-iScrollAt);
     var destination = document.getElementById("typedtext");
     
     while ( iRow < iIndex ) {
      sContents += aText[iRow++] + '<br />';
     }
     destination.innerHTML = sContents + aText[iIndex].substring(0, iTextPos) + "_";
     if ( iTextPos++ == iArrLength ) {
      iTextPos = 0;
      iIndex++;
      if ( iIndex != aText.length ) {
       iArrLength = aText[iIndex].length;
       setTimeout("typewriter()", 500);
      }
     } else {
      setTimeout("typewriter()", iSpeed);
     }
    }
    
    
    typewriter();`;
    script.async = true;

    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  let audio = new Audio("music/Any-Spring-Melody.mp3");
  audio.play();

  return (
    <div className="letter-container">
      <div id="typedtext"></div>
    </div>
  );
}

export default Letter;
